.about_container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.about_me{
    width: 21rem;
    height: 25rem;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg, transparent,var(--color-primary),transparent
    );
    display: grid;
    place-items: center;
}
.about_me_img {
    width: 21rem;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about_me_img:hover{
    transform: rotate(0);
}
.about_cards {
    display: flex;
    gap: 1.5rem;
}
.about_card {
    background: var(--color-primary-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1.5rem 3rem ;
    text-align: center;
    transition: var(--transition);
}
.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}
.about_icon {
    color: var(--color-primary);
    font-size: 2.4rem;
    margin-bottom: 1rem;

}
.about_card h5 {
    font-size: 0.95rem;
}
.about_card small {
    font-size: 0.7rem; color: var(--color-light);

}
.about_contant p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

@media screen and (max-width : 1024px) {
    .about_container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about_me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_cards{
        justify-content: center;
    }
    .about_card{
        width: 40%;
    }

    .about_contant p {
        margin: 1rem 0 1.5rem;
    }

 }

 @media screen and (max-width : 480px) {
    .about_me {
        width: 50%;
        text-align: center;
        margin: 0 1.75rem 5rem;
    }
    .about_cards {
        flex-direction: row;
        gap: 1rem;
       justify-content: center;

    }
    .about_card{

        width: 40%;
    }
    .about_contant {
        text-align: center;
        
    }
 
    .about_contant p {
        margin: 1.5rem 0;
        font-size: 10px;
    }


 }
